    const store = useStore();
    let dataRowList = ref(computed(() => store.getters.getDataRowList));

    // 현재 index row의 타입 형태를 동적으로 치환
    let dataRowListLength = ref(computed(() => dataRowList.value.length));

    // 특정 Data Row 삭제
    const deleteDataRow = index => {
      store.commit('deleteDataRow', {
        index: index,
      });
    };

    return {
      dataRowList,
      dataRowListLength,
      deleteDataRow,
    };
  },
});
